@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Condensed:wght@300;400;500;700&family=IBM+Plex+Sans:wght@100;300;400;700&display=swap);
body {
  margin: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: "IBM Plex Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: black;

  /* Credits to @pawel_czerwinski - unsplash */
  background-image: url(/static/media/background.4622c418.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
}

.gradient-top {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 200px;
  z-index: -1;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.gradient-bot {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 200px;
  z-index: -1;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header{padding:60px;display:flex;justify-content:space-between;align-items:center}.header .logo{font-family:"IBM Plex Sans Condensed";font-size:18px;font-weight:400;color:#ffffff}.header .logo span{font-weight:500}.header .social{display:flex;justify-content:space-between;align-items:center;width:160px;height:20px}.header .social a{color:#ffffff;text-decoration:none;opacity:0.6}.header .social a:hover{opacity:1}@media only screen and (max-width: 1000px){.header{padding:20px}.header .social{position:absolute;bottom:20px;left:50%;transform:translateX(-50%)}}

.sidebar{position:absolute;top:50%;left:60px;transform-origin:top left;transform:rotate(-90deg) translate(-50%, -50%)}.sidebar a{color:#fff;text-decoration:none;font-size:16px;font-weight:100;margin:10px;opacity:0.8}.sidebar a:hover{opacity:1}.sidebar .active{font-weight:500;opacity:1}@media only screen and (max-width: 1000px){.sidebar{position:absolute;top:22px;right:20px;left:auto;transform:none}}

.footer{position:absolute;bottom:0;left:0;right:0;padding:60px;display:flex;justify-content:space-between;font-size:16px;font-family:"IBM Plex Sans Condensed";font-weight:100;opacity:0.8}@media only screen and (max-width: 1000px){.footer{display:none}}

.about{display:flex;justify-content:center;width:80vw;max-height:80vh;font-size:16px;position:absolute;font-family:"IBM Plex Sans Condensed";top:50%;left:50%;transform:translate(-50%, -50%);background-color:rgba(0,0,0,0.4);box-shadow:0px 4px 8px rgba(0,0,0,0.15);overflow:auto;pointer-events:initial}.about h4{font-size:18px;padding-bottom:20px}.about>div{width:50%}.about .info{padding:30px}.about a{text-decoration:none;color:#ffffff;font-weight:600}.about ul{list-style-type:none}.about p,.about ul{line-height:1.4em}@media only screen and (max-width: 640px){.about{flex-direction:column;justify-content:initial;left:20px;right:20px;width:auto;transform:translateY(-50%)}.about>div{width:100%}.about .info{margin:0px 20px}}

.workitm{display:flex;position:absolute;left:17%;right:5%;top:50%;max-height:calc(100% - 240px);transform:translateY(-50%);transition:transform 0.15s ease-in}.workitm:hover{transform:translateY(-50%) scale(1.05)}.workitm:hover .image{transform:translateX(-2%);-webkit-filter:brightness(0.7);filter:brightness(0.7)}.workitm .info{font-size:18px}.workitm .info .desc{margin-bottom:20px}.workitm .text{display:flex;flex-direction:column;justify-content:center;width:40%;max-height:100%}.workitm .image{position:relative;max-height:100%;max-width:100%;overflow:hidden;transition:transform 0.15s ease-in;object-fit:contain;-webkit-filter:grayscale(1) brightness(0.4);filter:grayscale(1) brightness(0.4)}.title{position:relative;font-weight:bold;width:60vw;font-size:6vw}.outline{position:absolute;top:0;left:0;color:transparent;-webkit-text-stroke:1px white;z-index:5}.outline.prev,.outline.next{max-width:83%;left:17%;opacity:0.4;font-size:4.61538vw}.outline.prev{transform:translateY(-40%)}.outline.next{top:auto;bottom:0;transform:translateY(40%)}@media only screen and (max-width: 1000px){.workitm{flex-direction:column;left:40px;right:40px;top:80px;bottom:80px;max-height:100%;padding:0;transform:none;justify-content:space-evenly}.workitm:hover{transform:scale(1.05)}.workitm:hover .image{transform:none}.workitm .text{display:block;text-align:center;align-items:center;width:100%}.workitm .info{text-align:left;font-size:14px;padding:20px 0px;text-align:center}.workitm .info .desc{margin-bottom:10px}.workitm .info .date{font-size:13px;font-style:italic}.workitm .title{width:100%}.workitm .title .outline{display:none}.workitm .image{margin-top:20px;height:auto}}

.chevron{pointer-events:initial;display:flex;width:60px;height:60px;top:50%;transform:translateY(-50%);position:absolute;align-items:center;justify-content:center;z-index:3}.chevron>*{opacity:0.5}.chevron:hover>*{opacity:1}.chevron.next{right:0;background:linear-gradient(to left, rgba(0,0,0,0.6), rgba(0,0,0,0))}.chevron.prev{background:linear-gradient(to right, rgba(0,0,0,0.6), rgba(0,0,0,0))}

.carousel{position:fixed;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.9);z-index:9;pointer-events:initial}.carousel img{max-width:100%;max-height:100%;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.carousel .close{position:absolute;padding:20px;right:0;top:0;background-color:rgba(0,0,0,0.3);border-radius:100%;pointer-events:initial;opacity:0.8;z-index:10}.carousel .close:hover{opacity:1}

